@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500&display=swap);
body::-webkit-scrollbar {
  display: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Quicksand', sans-serif;
  font-weight: 300;
}

h1,h2,h3,h4,h5,p {
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
}

.login {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
}

.login > img {
    width: 50%;
}

.login > a {
    padding: 20px;
    background-color: #1db954;
    border-radius: 99px;
    font-weight: 800;
    color: white;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
}
.player-body {
    display: flex;
}
.sidebar {
    border: 1px solid black;
    height: 100vh;
    flex: 0.2 1;
    min-width: 230px;
    background-color: #040404;
    color: white;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
}

.sidebar > hr {
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;
}

.sidebar-logo {
    margin-right: auto;
    object-fit: contain;
    height: 70px;
    padding: 10px;
}
.sidebar-title {
    margin-left: 10px;
    padding: 5px;
    font-size: 12px;
}
.sidebarOptions {
    display: flex;
    align-items: center;
    color: grey;
    height: 40px;
    cursor: pointer;
    transition: 200ms color ease-in;
}

.sidebarOptions:hover {
    color: white;
}

.sidebarOptions > h4 {
    padding-left: 10px;
}

.sidebarOptions > p {
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}

.sidebarOption-icon {
    padding-left: 10px;

}
.body {    
    flex: 0.8 1;
    height: 100vh;    
    background: linear-gradient(rgb(91,87,115), rgba(0, 0, 0, 1));
    color: white;
    overflow-y: overlay;
    padding: 30px;
} 

.body-info {
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body-info > img {
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}

.body-infoText {
    flex: 1 1;
}

.body-infoText > h2 {
    font-size: 48px;
    margin-bottom: 10px;
}

.body-infoText > p {
    font-size: 14px;
}

.body-songs {
    margin: 20px -30px;
}

.body-icon {
    display: flex;
    align-items: center;
}

.body-icon > .MuiSvgIcon-root {
    margin-right: 20px;
}

.body-shuffle {
    font-size: 80px !important;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.body-shuffle:hover {
    transition: 0.2s transform ease-in;
    transform: scale(1.08);
    cursor: pointer;
}





.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.header-left {
    flex: 0.5 1;
    min-width: 70px;
    background-color: white;
    color: gray;
    border-radius: 30px;
    padding: 10px;
    display: flex;
    align-items: center;
}

.header-left > input {
    border: none;
    width: 100%;
    outline-width: 0;
}

.header-right {
    display: flex;
    align-items: center;
}

.header-right > h4 {
    margin-left: 10px;
    font-size: 15px;
}

/* Media Query */
@media screen and (max-width: 400px) {
    .body {
        padding: 5px;
    }
    .header {
        margin: 2px;
    }
    .header-right > h4 {
        font-size: 13px;
    }
}
.songRow {
    margin-left: 20px;
    padding: 20px;
    display: flex;
    align-items: center;
    z-index: 100;
    color: white;
}

.songRow:hover {
    cursor: pointer;
    background-color: black;
    opacity: 0.8;
}

.songRow-info {
    margin-left: 20px;
}

.songRow-info > h1 {
    font-size: 16px;
}

.songRow-info > p {
    font-size: 14px;
    margin-top: 3px;
    color: gray;
}

.songRow-album {
    height: 40px;
    width: 40px;
}
.footer {
    position: fixed;
    display: flex;
    justify-content: space-between;
    bottom: 0;
    height: 65px;
    width: 100%;
    background-color: #282828;
    padding: 20px;
} 

.footer-left {
    flex: 0.3 1;
    display: flex;
    align-items: center;
    color: white;
    width: 300px;
}

.footer-albumLogo {
    height: 60px;
    width: 60px;
    margin-right: 20px;
    object-fit: contain;
}

.footer-center {
    flex: 0.4 1;
    padding: 0 100px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 500px;
}

.footer-right {
    flex: 0.3 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}

.footer-green {
    color: #1ed15e;
}

.footer-songInfo > h4 {
    margin-bottom: 5px;
}
  
.footer-songInfo > p {
    font-size: 12px;
}

.footer-icon:hover,
.footer-green:hover {
    transition: transform 0.2s ease-in-out;
    transform: scale(1.2) !important;
    cursor: pointer;
}

.footer-right * .MuiSlider-root {
    color: #1ed15e;
}
