.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.header-left {
    flex: 0.5;
    min-width: 70px;
    background-color: white;
    color: gray;
    border-radius: 30px;
    padding: 10px;
    display: flex;
    align-items: center;
}

.header-left > input {
    border: none;
    width: 100%;
    outline-width: 0;
}

.header-right {
    display: flex;
    align-items: center;
}

.header-right > h4 {
    margin-left: 10px;
    font-size: 15px;
}

/* Media Query */
@media screen and (max-width: 400px) {
    .body {
        padding: 5px;
    }
    .header {
        margin: 2px;
    }
    .header-right > h4 {
        font-size: 13px;
    }
}